:root {
    --primary-color: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary-color);
    color: #242424;
    border: 1px solid var(--primary-color)
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    border: 1px solid var(--primary-color);
    transition: all 0.3s ease-out;
}

.btn--form {
    padding: inherit  26px inherit 26px;
    font-size: 20px;
    cursor: pointer;
    background-color: #242424;
    color: var(--primary-color);
    border: none;
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 12px 20px;
    font-size: 20px
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background-color: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}

.btn--form:hover {
    background-color: transparent;
    color: black;
    border: 1px solid #242424;
}
