.about-me{
    min-height: 50vh;
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    color: #fff;
    border-bottom: 2px solid rgba(56, 125, 255, 0.17);
    padding: 2rem;
}

.about-me .section-wrapper {
    display: flex;
    max-width: 1500px;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.about-me .title-area {
    font-size: 4rem;
}

.about-me .title-area h4 {
    margin: 2rem 0 2rem 0
}

.about-me .title-area p {
    font-size: 2rem;
}

.about-me .content-area {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    margin-bottom: 5rem;
}

.content-area .image-area {
    flex-basis: 50%;
    margin: 5rem 0 5rem 0
}

.content-area .aboutme-text-area {
    flex-basis: 50%;
    font-size: 2rem;
    height: 100%;
    text-align: center;
}

.content-area .image-area {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.aboutme-image {
    height: 450px;
    width: 450px;
    border-radius: 50%;
    border: 4px solid #fff;
}


@media screen and (max-width: 700px) {

    .about-me .title-area {
        font-size: 2rem;
    }

    .aboutme-image {
        height: 350px;
        width: 350px;
    }

    .content-area .aboutme-text-area {
        font-size: 1.3rem;
    }

}

@media screen and (max-width: 375px) {
    .aboutme-image {
        height: 300px;
        width: 300px;
    }
}